.commonFooter {
  position: fixed;
  z-index: 5;
  bottom: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 50px;
  box-shadow: 0 -5px 7.5px 0 rgba(0, 0, 0, 0.1);
}

.commonFooter .online,
.commonFooter .call {
  font-size: 17px;
  line-height: 50px;
  flex: 1;
  text-align: center;
  text-decoration: none;
  background: #fff;
}

.commonFooter .online {
  display: flex;
  align-items: center;
  margin-right: -25px;
  justify-content: center;
  background: #1d5662;
}

.commonFooter .online .msgCount {
  font-size: 13px;
  font-weight: bold;
  line-height: 18px;
  display: inline-block;
  overflow: hidden;
  width: 18px;
  height: 18px;
  margin-left: 5px;
  text-align: center;
  color: #fff;
  border: 2px solid #fff;
  border-radius: 50%;
  background: #f50;
}

.commonFooter .call {
  margin-left: -25px;
  background: #1d5662;
  color: #fff;
}

.commonFooter .icon {
  font-size: 17px;
  margin-right: 10px;
}

.commonFooter .logo {
  position: relative;
  z-index: 1;
  width: 50px;
  height: 50px;
  pointer-events: none;
}

.commonFooter.android .online .msgCount {
  line-height: 20px;
}