@import '//at.alicdn.com/t/font_1419779_pyj58acy64.css';

html,
body {
  scroll-behavior: smooth;
}

html {
  color: #333;
  -webkit-font-smoothing: antialiased;
}

body {
  font-size: 16px;
  line-height: 1.5;
  margin: 0;
}

:global .heading {
  font-size: 25px;
  font-weight: bold;
  line-height: 22px;
  position: relative;
  margin: 0;
  padding: 50px 20px 25px;
  color: #222;
}

:global .heading .subheading {
  font-size: 16px;
  font-weight: normal;
  display: block;
  margin: 11px 0 0;
  word-break: break-all;
  color: rgba(51, 51, 51, 0.75);
}

:global .heading::before {
  position: absolute;
  top: 50px;
  left: 0;
  width: 5px;
  height: 22.5px;
  content: '';
  background: #333;
}

:global .heading.white {
  color: #fff;
}

:global .heading.white .subheading {
  color: rgba(255, 255, 255, 0.75);
}

:global .heading.white::before {
  background: #fff;
}

:global #cvd-open {
  display: none;
}