.contact {
  overflow: hidden;
  background: #eef6ff;
}

.contact .noStatus {
  text-decoration: none;
  color: inherit;
}

.contact .form {
  padding: 0 20px;
}

.contact .form input {
  font-size: 17px;
  display: block;
  width: 100%;
  height: 50px;
  margin-bottom: 15px;
  padding: 0;
  text-indent: 15px;
  border: none;
  border-radius: 0;
  outline: none;
  background: rgba(255, 255, 255, 0.6);
}

.contact .form input:valid {
  background: #fff;
}

.contact .form .submitBtn {
  font-size: 17px;
  width: 100%;
  height: 50px;
  margin: 0 0 113px;
  padding: 0;
  color: #333;
  border: none;
  outline: none;
  background-color: #fff;
}

.submitModal {
  position: fixed;
  z-index: 101;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.submitModal .wrapper {
  position: relative;
  z-index: 1;
  width: 335px;
  text-align: center;
  border-radius: 2px;
  background: #fff;
}

.submitModal .wrapper .main {
  overflow: hidden;
}

.submitModal .wrapper .main .iconSuccess {
  font-size: 56px;
  display: block;
  margin: 30px 0 0;
  color: #1d5662;
}

.submitModal .wrapper .main .result {
  font-size: 18px;
  font-weight: bold;
  margin: 20px 0 0;
  color: #1d5662;
}

.submitModal .wrapper .main .desc {
  font-size: 16px;
  margin: 13.5px 0 40px;
  color: #999;
}

.submitModal .wrapper .btnClose {
  font-size: 17px;
  width: 100%;
  height: 45px;
  color: #1d5662;
  border: none;
  outline: none;
  background: #d7e6f5;
}

.submitModal::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: '';
  background: rgba(0, 0, 0, 0.5);
}