@keyframes downIconSlide {
  0% {
    transform: translate3d(0, 0, 0);
    opacity: 0.6;
  }

  40% {
    transform: translate3d(0, 10px, 0);
    opacity: 1;
  }

  50% {
    transform: translate3d(0, 8px, 0);
    opacity: 0.9;
  }

  60% {
    transform: translate3d(0, 10px, 0);
    opacity: 1;
  }

  100% {
    transform: translate3d(0, 0, 0);
    opacity: 0.6;
  }
}

.index .cover {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  overflow: hidden;
  flex-direction: column;
  width: 100%;
  height: calc(100% - 50px);
  color: #fff;
  background-color: rgba(0, 0, 0, 0.5);
}

.index .cover::after {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: '';
  opacity: 0.9;
  background-image: url('@/static/images/bg01.jpg');
  background-size: cover;
}

.index .cover .main {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.index .cover .main .slogan {
  display: block;
  width: 300px;
  height: 100px;
}

.index .cover .main .brick {
  width: 30px;
  height: 4px;
  margin: 20px auto;
  background: #fff;
}

.index .cover .main p {
  padding: 20px;
  font-size: 16px;
  text-align: center;
}

.index .cover .effects {
  display: flex;
  margin: 0 10px 40px;
}

.index .cover .effects .item {
  font-size: 18px;
  font-weight: bold;
  line-height: 1;
  flex: 1;
  text-align: center;
  color: #fff;
}

.index .cover .effects .item .unit {
  font-size: 6px;
}

.index .cover .effects .item .txt {
  font-size: 13px;
  font-weight: normal;
  display: block;
  margin-top: 12px;
  color: rgba(255, 255, 255, 0.75);
}

.index .cover .down {
  font-size: 23px;
  margin: 0 auto 25px;
  animation: downIconSlide 1s ease infinite;
  color: rgba(255, 255, 255, 0.5);
}

.index .features {
  position: absolute;
  top: calc(100% - 50px);
  width: 100%;
}

.index .industryCase {
  padding-bottom: 35px;
  background: #fff;
}

.index .productCase {
  padding-bottom: 25px;
}

.index .productCase .caseGroup {
  overflow: hidden;
  margin: 0 20px 10px;
  padding: 0 20px;
  color: #333;
  background: #eef6ff;
}

.index .productCase .caseGroup header {
  font-size: 17px;
  font-weight: bold;
  line-height: 50px;
  margin: 0 0 -1px;
  border-bottom: 1px solid #666;
}

.hairlines .index .productCase .caseGroup header {
  margin: 0 0 -0.5px;
  border-bottom: 0.5px solid #666;
}

.index .productCase .caseGroup header .iconCollapse {
  float: right;
  transition: transform ease 0.3s;
}

.index .productCase .caseGroup .featureList {
  overflow: hidden;
  transition: height ease 0.3s;
}

.index .productCase .caseGroup .featureList .featureItem {
  word-break: break-all;
  font-size: 14px;
  position: relative;
  margin: 20px 0;
  padding-left: 26px;
  color: #333;
}

.index .productCase .caseGroup .featureList .featureItem .iconGou {
  font-size: 12px;
  position: absolute;
  top: 1.5px;
  left: 0;
}

.index .productCase .caseGroup.collapse header .iconCollapse {
  transform: rotate(180deg);
}

.index .productCase .caseGroup.collapse .featureList {
  height: 0;
}

.index .servicesCase {
  overflow: hidden;
  padding-bottom: 35px;
  background: #fff;
}

.index .servicesCase .matchImg {
  display: block;
  width: 310px;
  margin: 0 auto 18px;
  transform: translate3d(5px, 0, 0);
}

.index .supportCase {
  overflow: hidden;
  padding-bottom: 35px;
  background: #eef6ff;
}

.index .supportCase .platform {
  display: flex;
  justify-content: space-between;
  margin: 0 20px;
}

.index .supportCase .platform .item {
  overflow: hidden;
  width: 77.5px;
  height: 124px;
  text-align: center;
  background: #fff;
}

.index .supportCase .platform .item .matchImg {
  display: block;
  width: 36px;
  margin: 24px auto 0;
}

.index .supportCase .platform .item .name {
  font-size: 14px;
  margin: 12px 0 0;
}

.index .supportCase .platform .item .en {
  font-size: 10px;
  margin: 7.5px 0 0;
  color: #999;
  text-transform: lowercase;
}

.index .partnerCase {
  padding-bottom: 35px;
  background: #fff;
}

.index .partnerCase .partnerList {
  display: flex;
  flex-wrap: wrap;
  margin: 0 20px;
}

.index .partnerCase .partnerList .partnerItem {
  position: relative;
  box-sizing: border-box;
  width: 25%;
  border-top: 1px solid #e8e8e8;
  border-left: 1px solid #e8e8e8;
}

.hairlines .index .partnerCase .partnerList .partnerItem {
  border-top: 0.5px solid #e8e8e8;
  border-left: 0.5px solid #e8e8e8;
}

.index .partnerCase .partnerList .partnerItem::before {
  position: absolute;
  top: -1px;
  right: -1px;
  bottom: -1px;
  width: 1px;
  content: '';
  background: #e8e8e8;
}

.hairlines .index .partnerCase .partnerList .partnerItem::before {
  top: -0.5px;
  right: -0.5px;
  bottom: -0.5px;
  width: 0.5px;
}

.index .partnerCase .partnerList .partnerItem::after {
  position: absolute;
  right: -1px;
  bottom: -1px;
  left: -1px;
  height: 1px;
  content: '';
  background: #e8e8e8;
}

.hairlines .index .partnerCase .partnerList .partnerItem::after {
  right: -0.5px;
  bottom: -0.5px;
  left: -0.5px;
  height: 0.5px;
}

.index .partnerCase .partnerList .partnerItem .partnerLogo {
  display: block;
  width: 100%;
  min-height: 40px;
}

.index .cases {
  display: flex;
  flex-wrap: wrap;
  margin: 0 7.5px;
}

.index .cases .case {
  width: calc(50% - 25px);
  margin: 0 12.5px;
  padding: 15px 0;
  border-bottom: 1px solid #e5e5e5;
}

.hairlines .index .cases .case {
  border-bottom: 0.5px solid #e5e5e5;
}

.index .cases .case:nth-child(1),
.index .cases .case:nth-child(2) {
  border-top: 1px solid #e5e5e5;
}

.hairlines .index .cases .case:nth-child(1),
.hairlines .index .cases .case:nth-child(2) {
  border-top: 0.5px solid #e5e5e5;
}

.index .cases .case h3 {
  font-size: 17px;
  margin: 0;
  color: #333;
}

.index .cases .case p {
  font-size: 15px;
  margin: 5px 0 0;
  color: #666;
  word-break: break-all;
}