@keyframes slideDown {
  0% {
    height: 50px;
  }

  100% {
    height: 100%;
  }
}

@keyframes slideUp {
  0% {
    height: 100%;
    background: #fff;
  }

  100% {
    height: 50px;
    background: #fff;
  }
}

@keyframes slideUp2 {
  0% {
    height: 100%;
    background: #fff;
  }

  75% {
    background: #fff;
  }

  100% {
    height: 50px;
    background: transparent;
  }
}

.navigator {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  overflow: hidden;
  width: 100%;
  height: 50px;
}

.navigator .header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 50px;
  padding: 0 15px 0 20px;
  transition: background 0.3s ease;
}

.navigator .header .logo {
  color: #fff;
  font-size: 18px;
  transition: all 0.3s ease;
  background-size: cover;
}

.navigator .header .menuTrigger {
  font-size: 19px;
  line-height: 1;
  padding: 5px;
  transition: color 0.3s ease;
  color: #fff;
}

.navigator.noAnimation {
  animation: none !important;
}

.navigator.slideDown {
  animation: slideDown 0.3s ease;
  background: #fff;
}

.navigator.slideDown .header {
  box-shadow: none !important;
}

.navigator.slideDown .header .logo {
  color: #333;
}

.navigator.slideDown .header .menuTrigger {
  color: #333;
}

.navigator.slideUp {
  animation: slideUp2 0.3s ease;
}

.navigator.fixed {
  box-shadow: 0 2.5px 5px 0 rgba(0, 0, 0, 0.15);
}

.navigator.fixed .header {
  background: #fff;
}

.navigator.fixed .header .logo {
  color: #333;
}

.navigator.fixed .header .menuTrigger {
  color: #333;
}

.navigator.fixed.slideUp {
  animation: slideUp 0.3s ease;
}

.navigator .dropdown {
  padding: 35px 50px;
}

.navigator .dropdown .menu {
  font-size: 20px;
  font-weight: bold;
  line-height: 70px;
  border-top: 1px solid #e8e8e8;
}

.navigator .dropdown .menu.active {
  color: #1d5662;
}

.navigator .dropdown .menu:last-child {
  border-bottom: 1px solid #e8e8e8;
}

.navigator .dropdown .menu span {
  font-weight: normal;
  float: right;
  color: #ccc;
}